.product {
  background-color: $whiteColor;
  padding-top: 121px;
  padding-bottom: 98px;

  @media screen and (min-width: 767px) {
    padding-top: 122px;
    padding-bottom: 112px;
  }

  @media screen and (min-width: 1280px) {
    padding-top: 127px;
    padding-bottom: 119px;
  }

  &__title {
    margin-bottom: 15px;

    font-family: $tittleFont;
    font-size: 18px;
    line-height: 1.17;
    text-align: center;
    text-transform: uppercase;
    color: $preTittleColor;

    @media screen and (min-width: 768px) {
      font-size: 26px;
      line-height: 1.15;
    }

    @media screen and (min-width: 1280px) {
      font-size: 30px;
      line-height: 1.13;
      letter-spacing: 0.04em;
    }
  }

  &__heading {
    margin-bottom: 144px;

    font-family: $tittleFont;
    font-size: 34px;
    line-height: 1.15;
    letter-spacing: 0.04em;
    text-align: center;
    text-transform: uppercase;
    color: $accentColor;

    @media screen and (min-width: 768px) {
      margin-bottom: 150px;
      font-size: 48px;
      line-height: 1.14;
    }

    @media screen and (min-width: 1280px) {
      margin-bottom: 259px;
      font-size: 58px;
    }
  }
}

.product__list {
  @media screen and (min-width: 768px) {
    display: flex;
    margin: -10px;
  }

  @media screen and (min-width: 1280px) {
    margin: -12px;
  }
}

.product-card {
  position: relative;

  padding-top: 160px;
  padding-bottom: 41px;
  border-radius: 24px;

  &:not(:last-child) {
    @media screen and (max-width: 767px) {
      margin-bottom: 130px;
    }
  }

  @media screen and (min-width: 768px) {
    padding-top: 152px;
    padding-bottom: 40px;
    margin: 10px;
    flex-basis: calc((100% - 3 * 20px) / 3);
  }

  @media screen and (min-width: 1280px) {
    padding-top: 163px;
    padding-bottom: 50px;
    margin: 12px;
    flex-basis: calc((100% - 3 * 24px) / 3);
  }

  &__heading {
    font-family: $tittleFont;
    font-size: 20px;
    line-height: 1.15;
    letter-spacing: 0.04em;
    text-align: center;
    text-transform: uppercase;
    color: $whiteColor;
  }

  &__heading::after {
    display: block;
    margin: 30px auto;
    content: '';
    width: 26px;
    height: 6px;
    background-image: url(../images/dots.png);

    @media (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) {
      background-image: url(../images/dots@2x.png);
    }
  }

  @media screen and (min-width: 1280px) {
    font-size: 30px;
    line-height: 1.13;
  }

  &::before {
    position: absolute;
    top: -113px;
    left: 50%;
    transform: translateX(-50%);

    display: block;
    content: '';
    width: 249px;
    height: 252px;
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
  }
}

.product-card--cream {
  background-color: $heroBg;

  &::before {
    background-image: url(../images/mobile/products-icecream-mobile.png);

    @media (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) {
      background-image: url(../images/mobile/products-icecream-mobile@2x.png);
    }

    @media screen and (min-width: 768px) {
      width: 220px;
      height: 233px;
      background-image: url(../images/tablet/products-icecream-tablet.png);

      @media (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) {
        background-image: url(../images/tablet/products-icecream-tablet@2x.png);
      }
    }

    @media screen and (min-width: 1280px) {
      top: -210px;
      width: 335px;
      height: 335px;
      background-image: url(../images/desktop/products-icecream-desktop.png);

      @media (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) {
        background-image: url(../images/desktop/products-icecream-desktop@2x.png);
      }
    }
  }
}

