.product-card__text {
  width: 200px;
  margin: 0 auto;
  margin-bottom: 30px;
  text-align: center;

  font-weight: 700;
  font-size: 14px;
  line-height: 1.5;
  letter-spacing: 0.04em;
  color: $whiteColor;

  @media screen and (min-width: 768px) {
    width: 198px;
    margin-bottom: 40px;
    line-height: 1.78;
  }

  @media screen and (min-width: 1280px) {
    width: 241px;
    margin-bottom: 62px;
    font-size: 16px;
    line-height: 1.75;
  }
}

.product-card__btn {
  display: block;
  border: none;
  margin: auto;
  padding: 0;
  width: 40px;
  height: 40px;
  border-radius: 50%;

  cursor: pointer;
}

.btn__icon {
  display: block;
  margin: auto;
  width: 15px;
  height: 15px;
  fill: $accentColor;
}
